@import "./index.css";

.content:has(.cv) {
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: flex-start;
  gap: initial;
}

p.cv {
  padding: 0;
}

.cv.specs {
  font-style: italic;
  margin: 0.1em 0;
  padding: 0;
  font-weight: 400;
}

.cv.header {
  margin: 0.5em 0;
}

.cv.para {
  margin: 1em 0 0;
}

h3.cv.subhead {
  margin: 1em 0 0.5em;
}

h3.cv.subhead + p {
  margin: 0;
}
