@import "./index.css";

.project {
  padding: 0;
  margin: 0;
  flex: 1 1 auto;
}
.project.header,
.project.para,
.project.intro,
.project.subhead {
  margin: 2vh 0;
}
/* =================================== */
/* ============BACK BUTTON============ */
/* =================================== */
.project.back {
  flex: 0 0 100%;
  background-color: var(--main-light-color);
}
.project.backLink {
  padding: 0.5em;
  width: fit-content;
  display: flex;
  flex-flow: row nowrap;
  justify-content: left;
  align-items: center;
  gap: 0.5em;
}
.project.back:first-of-type {
  order: -100;
}
.project.back:last-of-type {
  order: 100;
}
.project.backLink:hover {
  border: var(--main-hover-border);
  box-shadow: var(--main-hover-box-shadow);
}

.project.backArrow {
  flex: 0 0 0.9em;
  height: auto;
  margin: 0.1em 0.1em 0.2em;
}

.project.backLink > * {
  vertical-align: middle;
}

/* =================================== */
/* ==============IMAGES=============== */
/* =================================== */
.project.pic {
  display: flex;
  justify-content: center;
  align-items: center;
}

.project.pic img {
  width: 100%;
  height: auto;
}

.project.pic:has(.img) {
  overflow: hidden;
  max-height: 25vh;
  width: 50%;
  float: right;
  margin: 1em;
}

@media screen and (orientation: portrait) {
  .project.pic:has(.img) {
    width: 90%;
    float: initial;
  }
}

/* =================================== */
/* =============HEADINGS============== */
/* =================================== */

.project.pic:has(.mainimg) {
  flex: 1 1 calc(50vmin - 0.5em);
  overflow: hidden;
  max-height: 25vh;
}

.project.subhead {
  flex: 0 0 100%;
}

.project.intro {
  font-weight: 400;
  font-style: italic;
}
